  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  line-height: 1.5;
  font-family: system-ui;
  --green-color: #059669;
  --grey-color: #e2e8f0;
  --gold-color: #f7ad2d;
  --boxshadow: 2px 2px 4px 1px rgb(0 0 0 / 10%);
}

.loader {
  border: 10px solid #f3f3f3;
  border-top: 10px solid #3498db; 
  border-radius: 50%;
  width: 50px;
  height: 50px;
  animation: spin 2s linear infinite;
}

